@import url(https://db.onlinewebfonts.com/c/0b811799540d4b23474c8cd3c9576879?family=FF+Tisa+Sans);
@font-face {font-family: "FF Tisa Sans";
    src: url("https://db.onlinewebfonts.com/t/0b811799540d4b23474c8cd3c9576879.eot");
    src: url("https://db.onlinewebfonts.com/t/0b811799540d4b23474c8cd3c9576879.eot?#iefix") format("embedded-opentype"),
    url("https://db.onlinewebfonts.com/t/0b811799540d4b23474c8cd3c9576879.woff2") format("woff2"),
    url("https://db.onlinewebfonts.com/t/0b811799540d4b23474c8cd3c9576879.woff") format("woff"),
    url("https://db.onlinewebfonts.com/t/0b811799540d4b23474c8cd3c9576879.ttf") format("truetype"),
    url("https://db.onlinewebfonts.com/t/0b811799540d4b23474c8cd3c9576879.svg#FF Tisa Sans") format("svg");
}

html,

body {
	line-height: 1.7em;
	color: aliceblue;
	background: #333;
	font-size: 24px;
	font-family: FF Tisa;
  }

.container {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

h1 {
	text-align: center;
}


Table {
	border: 2px solid #aaa;
	border-radius: 13px;
    margin: 8px 0;
	width: 100%;
	border-collapse: collapse;
	overflow: hidden;
	box-shadow: 0 0 20px #333;
}

.alert{
	font-size: 14px;
	height: 025;
}

th {
	text-align: left;
}

thead {
		background-color: rgb(63, 51, 107);
}

td {
	font-weight: bold;
}



#sann{
	width: 1px;
}

#frs {
  animation: blinker 1s linear infinite;
}

@keyframes blinker {  
  50% { opacity: 0; }
}

#san {
	margin:5px;
	display: block;
	width: 3px;
	height: 3px;
	border-radius: 50%;
	background: green;
	cursor: pointer;  
	animation: pulse 2s infinite;
	float: middle;
	
  }
  #san:hover {
	animation: none;
  }
  
  @keyframes pulse {
	0% {
	  -moz-box-shadow: 0 0 0 0 rgba(204,169,44, 0.4);
	  box-shadow: 0 0 0 0 rgba(204,169,44, 0.4);
	}
	70% {
		-moz-box-shadow: 0 0 0 30px rgba(204,169,44, 0);
		box-shadow: 0 0 0 30px rgba(204,169,44, 0);
	}
	100% {
		-moz-box-shadow: 0 0 0 0 rgba(204,169,44, 0);
		box-shadow: 0 0 0 0 rgba(204,169,44, 0);
	}
  }